import React, { useState, useEffect } from 'react';
import { Code, Users, Briefcase, Calendar, ChevronRight, ChevronLeft, ExternalLink } from 'lucide-react';

const GDSCWebsite = () => {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [currentTeamPage, setCurrentTeamPage] = useState(0);

  useEffect(() => {
    const handleMouseMove = (e) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };
    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  const Header = () => (
    <header className="fixed top-0 left-0 right-0 flex justify-between items-center py-4 px-8 bg-white bg-opacity-90 shadow-md z-10">
      <div className="w-12 h-12">
        <img src="/api/placeholder/48/48" alt="GDSC Logo" className="w-full h-full object-contain" />
      </div>
      <nav className="space-x-6">
        <a href="#home" className="text-gray-600 hover:text-blue-600 transition duration-300">Home</a>
        <a href="#events" className="text-gray-600 hover:text-red-600 transition duration-300">Events</a>
        <a href="#projects" className="text-gray-600 hover:text-yellow-600 transition duration-300">Projects</a>
        <a href="#team" className="text-gray-600 hover:text-green-600 transition duration-300">Team</a>
        <a href="https://forms.gle/exampleGoogleFormLink" target="_blank" rel="noopener noreferrer" className="bg-blue-600 text-white px-4 py-2 rounded-full hover:bg-blue-700 transition duration-300">Join Us</a>
      </nav>
    </header>
  );

  const MouseFollower = () => (
    <div 
      className="fixed pointer-events-none z-50 w-6 h-6 rounded-full border-2 border-blue-500 transition-all duration-100 ease-out"
      style={{ 
        left: `${mousePosition.x}px`, 
        top: `${mousePosition.y}px`,
        transform: 'translate(-50%, -50%)'
      }}
    ></div>
  );

  const Hero = () => (
    <section id="home" className="flex justify-between items-center min-h-screen px-8 pt-20">
      <div className="w-1/2 pr-12">
        <h1 className="text-6xl font-bold mb-6 leading-tight">
          <span className="text-blue-600">E</span>
          <span className="text-red-600">m</span>
          <span className="text-yellow-600">p</span>
          <span className="text-green-600">o</span>
          <span className="text-gray-800">wering</span> Students Through Technology
        </h1>
        <p className="text-gray-600 mb-8 text-xl">Join Google Developer Student Club to learn, share, and grow in a community of tech enthusiasts.</p>
        <div className="space-x-4">
          <a href="https://forms.gle/exampleGoogleFormLink" target="_blank" rel="noopener noreferrer" className="inline-block bg-blue-600 text-white px-8 py-3 rounded-full hover:bg-blue-700 transition duration-300 transform hover:scale-105 shadow-lg">Join Now</a>
          <button className="bg-white text-gray-800 px-8 py-3 rounded-full hover:bg-gray-100 transition duration-300 transform hover:scale-105 shadow-lg border border-gray-300">Learn More</button>
        </div>
      </div>
      <div className="w-2/5 bg-gradient-to-br from-blue-400 via-red-400 to-yellow-400 rounded-lg overflow-hidden shadow-2xl transform hover:scale-105 transition duration-500">
        <img src="/api/placeholder/500/300" alt="GDSC Event" className="w-full h-auto opacity-75 hover:opacity-100 transition duration-300" />
      </div>
    </section>
  );

  const Feature = ({ icon: Icon, title, description, color }) => (
    <div className={`bg-white rounded-lg p-6 flex flex-col items-center text-center transform hover:scale-105 transition duration-300 shadow-lg border-t-4 border-${color}-500`}>
      <Icon size={40} className={`text-${color}-500 mb-4`} />
      <h3 className="text-xl font-bold mb-2 text-gray-800">{title}</h3>
      <p className="text-gray-600">{description}</p>
    </div>
  );

  const Features = () => (
    <section className="my-20 px-8">
      <h2 className="text-4xl font-bold mb-12 text-center text-gray-800">What We Offer</h2>
      <div className="grid grid-cols-3 gap-8">
        <Feature 
          icon={Code}
          title="Hands-on Workshops"
          description="Learn practical skills through interactive coding sessions and projects."
          color="blue"
        />
        <Feature 
          icon={Users}
          title="Networking Opportunities"
          description="Connect with like-minded students and industry professionals."
          color="red"
        />
        <Feature 
          icon={Briefcase}
          title="Real-world Projects"
          description="Work on impactful projects and build your portfolio."
          color="yellow"
        />
      </div>
    </section>
  );

  const EventCard = ({ title, date, description, color }) => (
    <div className={`bg-white rounded-lg p-6 flex flex-col justify-between transform hover:scale-105 transition duration-300 shadow-lg border-l-4 border-${color}-500`}>
      <div>
        <h3 className="text-xl font-bold mb-2 text-gray-800">{title}</h3>
        <p className={`text-${color}-500 mb-4`}>{date}</p>
        <p className="text-gray-600 mb-4">{description}</p>
      </div>
      <button className={`flex items-center text-${color}-500 hover:text-${color}-600 transition duration-300`}>
        Learn more <ChevronRight size={20} className="ml-2" />
      </button>
    </div>
  );

  const Events = () => (
    <section id="events" className="my-20 px-8">
      <h2 className="text-4xl font-bold mb-12 text-center text-gray-800">Upcoming Events</h2>
      <div className="grid grid-cols-3 gap-8">
        <EventCard 
          title="Machine Learning Workshop"
          date="May 15, 2024"
          description="Learn the basics of ML and build your first model."
          color="blue"
        />
        <EventCard 
          title="Web Dev Bootcamp"
          date="June 1-3, 2024"
          description="A 3-day intensive course on modern web development."
          color="red"
        />
        <EventCard 
          title="Google Cloud Study Jam"
          date="June 20, 2024"
          description="Hands-on experience with Google Cloud Platform."
          color="yellow"
        />
      </div>
    </section>
  );

  const ProjectCard = ({ title, tech, description, color }) => (
    <div className={`bg-white rounded-lg p-6 flex flex-col justify-between transform hover:scale-105 transition duration-300 shadow-lg border-l-4 border-${color}-500`}>
      <div>
        <h3 className="text-xl font-bold mb-2 text-gray-800">{title}</h3>
        <p className={`text-${color}-500 mb-4`}>{tech}</p>
        <p className="text-gray-600 mb-4">{description}</p>
      </div>
      <button className={`flex items-center text-${color}-500 hover:text-${color}-600 transition duration-300`}>
        View Project <ChevronRight size={20} className="ml-2" />
      </button>
    </div>
  );

  const Projects = () => (
    <section id="projects" className="my-20 px-8">
      <h2 className="text-4xl font-bold mb-12 text-center text-gray-800">Our Projects</h2>
      <div className="grid grid-cols-3 gap-8">
        <ProjectCard 
          title="Community Health App"
          tech="React Native, Firebase"
          description="A mobile app to connect local health workers with community members."
          color="blue"
        />
        <ProjectCard 
          title="Smart Campus Navigation"
          tech="Flutter, Google Maps API"
          description="An interactive map and navigation system for university campuses."
          color="red"
        />
        <ProjectCard 
          title="AI Study Buddy"
          tech="Python, TensorFlow, Flask"
          description="An AI-powered chatbot to assist students with their studies."
          color="yellow"
        />
      </div>
    </section>
  );

  const TeamMember = ({ name, role, image, bio, color }) => {
    const [isHovered, setIsHovered] = useState(false);
  
    return (
      <div 
        className={`bg-white rounded-lg p-6 text-center transform transition duration-300 shadow-lg border-b-4 border-${color}-500 ${isHovered ? 'scale-105' : ''}`}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
      >
        <div className="relative w-48 h-48 mx-auto mb-4 overflow-hidden rounded-lg">
          <img src={image} alt={name} className="w-full h-full object-cover transition-transform duration-300 transform" style={{ transform: isHovered ? 'scale(1.1)' : 'scale(1)' }} />
          <div className={`absolute inset-0 bg-${color}-500 bg-opacity-90 flex items-center justify-center transition-opacity duration-300 ${isHovered ? 'opacity-100' : 'opacity-0'}`}>
            <p className="text-white text-sm px-4">{bio}</p>
          </div>
        </div>
        <h3 className="text-xl font-bold mb-1 text-gray-800">{name}</h3>
        <p className={`text-${color}-500`}>{role}</p>
      </div>
    );
  };

  const Team = () => {
    const teamMembers = [
      { name: "Alex Johnson", role: "GDSC Lead", image: "/api/placeholder/300/300", bio: "Passionate about bringing tech opportunities to campus!", color: "blue" },
      { name: "Samantha Lee", role: "Technical Lead", image: "/api/placeholder/300/300", bio: "Full-stack developer with a love for AI and machine learning.", color: "red" },
      { name: "David Chen", role: "Project Coordinator", image: "/api/placeholder/300/300", bio: "Turning ideas into impactful projects is my passion!", color: "yellow" },
      { name: "Emily Taylor", role: "Marketing Lead", image: "/api/placeholder/300/300", bio: "Spreading the word about GDSC and making tech accessible to all.", color: "green" },
      { name: "Michael Brown", role: "Event Organizer", image: "/api/placeholder/300/300", bio: "Creating unforgettable tech events is what I do best!", color: "blue" },
      { name: "Sophie Wang", role: "Design Lead", image: "/api/placeholder/300/300", bio: "Bringing creativity and aesthetics to our tech projects.", color: "red" },
      { name: "Ryan Patel", role: "Outreach Coordinator", image: "/api/placeholder/300/300", bio: "Building bridges between GDSC and the tech community.", color: "yellow" },
      { name: "Olivia Kim", role: "Content Creator", image: "/api/placeholder/300/300", bio: "Crafting compelling stories about our tech journey.", color: "green" },
      { name: "Ethan Nguyen", role: "Workshop Facilitator", image: "/api/placeholder/300/300", bio: "Empowering students through hands-on learning experiences.", color: "blue" },
      { name: "Isabella Garcia", role: "Community Manager", image: "/api/placeholder/300/300", bio: "Fostering a vibrant and inclusive tech community on campus.", color: "red" },
      { name: "Aiden Tran", role: "Tech Support", image: "/api/placeholder/300/300", bio: "Ensuring smooth sailing for all our tech initiatives.", color: "yellow" },
      { name: "Zoe Lewis", role: "Research Lead", image: "/api/placeholder/300/300", bio: "Exploring cutting-edge technologies for our projects.", color: "green" },
    ];
  
    const pageCount = Math.ceil(teamMembers.length / 4);
  
    const nextPage = () => {
      setCurrentTeamPage((prev) => (prev + 1) % pageCount);
    };
  
    const prevPage = () => {
      setCurrentTeamPage((prev) => (prev - 1 + pageCount) % pageCount);
    };
  
    return (
      <section id="team" className="my-20 px-8">
        <h2 className="text-4xl font-bold mb-12 text-center text-gray-800">Meet Our Team</h2>
        <div className="grid grid-cols-4 gap-8">
          {teamMembers.slice(currentTeamPage * 4, (currentTeamPage + 1) * 4).map((member, index) => (
            <TeamMember key={index} {...member} />
          ))}
        </div>
        <div className="flex justify-center mt-8 space-x-4">
          <button onClick={prevPage} className="bg-blue-600 text-white p-2 rounded-full hover:bg-blue-700 transition duration-300">
            <ChevronLeft size={24} />
          </button>
          <button onClick={nextPage} className="bg-blue-600 text-white p-2 rounded-full hover:bg-blue-700 transition duration-300">
            <ChevronRight size={24} />
          </button>
        </div>
      </section>
    );
  };

  return (
    <div className="relative">
      <MouseFollower />
      <Header />
      <Hero />
      <Features />
      <Events />
      <Projects />
      <Team />
    </div>
  );
};

export default GDSCWebsite;
